<template>
  <a-spin :spinning="loading" :tip="$t('task.tip')" size="large">
    <div :bordered="false">
      <a-card>
        <div class="table-page-search-wrapper" style="margin-bottom: 10px;">
          <a-row>
            <a-col :md="21" :sm="21">
              <a-button-group>
                <btn-add type="bt" :text="$t('loss.add')" @click="onCreate"></btn-add>
                <a-dropdown style="margin-top:2px" placement="bottomRight">
                  <a-menu slot="overlay">
                    <a-menu-item key="1">
                      <btn-add @click="onEdit" type="b" :text="$t('loss.edit')"/>
                    </a-menu-item>
                    <a-menu-item key="2">
                      <btn-add @click="onRemove" type="b" :text="$t('loss.delete')"/>
                    </a-menu-item>
                  </a-menu>
                  <a-button type="primary"><a-icon style="font-size: 18px" type="ellipsis" /></a-button>
                </a-dropdown>
              </a-button-group>&nbsp;&nbsp;&nbsp;
              <a-input-search v-model="queryParam.stationCode" :placeholder="$t('dataedit.station_placeholder')" @search="SelectSub" style="width: 220px;display:inline-block" />&nbsp;&nbsp;&nbsp;
              <a-input-search v-model="queryParam.code" :placeholder="$t('dataedit.maintrans_placeholder')" @search="SelectMain" style="width: 220px;display:inline-block" />
            </a-col>
            <a-col :md="3" :sm="3">
              <sorter @refresh="onSearch" @show="showSearch"></sorter>
            </a-col>
          </a-row>
        </div>
        <s-table  
          ref="table"
          :bordered="false"
          :autoLoad="true"
          :data="loadData"
          size="small"
          rowKey="stationMtId"
          :rowSelection="{type:'checkbox', fixed: false, onChange: onSelect,selectedRowKeys: selectedRowKeys}"
        >
          <a-table-column 
            :title="this.$t('task.index1')"
            width="50px"
            align="center"
          >
            <span slot-scope="text">
              <a-icon type="schedule" theme="twoTone" class="icon" style="font-size:25px"/>
            </span>
          </a-table-column>
          <a-table-column
            :title="this.$t('line.info1')"
            width="120px"
            dataIndex="stationName"
          >
          </a-table-column>
          <a-table-column
            :title="this.$t('maintransformer.code')"
            width="120px"
            dataIndex="stationMtCode"
          >
          </a-table-column>
          <a-table-column
            :title="this.$t('maintransformer.name')"
            width="150px"
            dataIndex="stationMtName"
          >
          </a-table-column>
          <a-table-column
            :title="this.$t('loss.index1')"
            width="150px"
          >
            <span slot-scope="text,record">
              <span v-if="record.generationWay=='0'">{{ $t('loss.addtype0') }}</span>
            </span>
          </a-table-column>
          <a-table-column
            :title="this.$t('loss.index2')"
            width="120px"
            dataIndex="supplyInCount"
          ></a-table-column>
          <a-table-column
            :title="this.$t('loss.index3')"
            width="120px"
            dataIndex="supplyOutCount"
          >
          </a-table-column>
          <a-table-column
            :title="this.$t('loss.index6')"
            width="150px"
            dataIndex="lossIndexDay"
          ></a-table-column>
          <a-table-column
            :title="this.$t('loss.index7')"
            width="150px"
            dataIndex="lossIndexMonth"
          >
          </a-table-column>
          <a-table-column
            :title="this.$t('loss.index8')"
            width="150px"
            dataIndex="generationFlag"
            :filters="filters"
            :filterMultiple="false"
          >
            <span slot-scope="text,record">
              <span v-if="record.generationFlag!='1'">{{ $t('common.no') }}</span>
              <span v-if="record.generationFlag=='1'">{{ $t('common.yes') }}</span>
            </span>
          </a-table-column>
          <a-table-column
            :title="this.$t('loss.index9')"
            width="150px"
          >
            <span slot-scope="text, record">
              <pick-label :defaultValue="record.generateTime" :showTime="false"/>
            </span>
          </a-table-column>
          
          <a-table-column
            :title="this.$t('task.action')"
            dataIndex="action"
          > 
            <span slot-scope="text,record">
              <a-icon type="plus" :style="{ fontSize: '16px', color: '#1890FF',cursor:'pointer'}" @click="onCreate(record)" :title="$t('loss.action1')"/>
              <span v-if="record.generationFlag=='1'">
                <a-divider type="vertical"/>
                <a-icon type="usergroup-add" :style="{ fontSize: '16px', color: '#1890FF',cursor:'pointer'}" @click="onAdd(record)" :title="$t('loss.action2')"/>
                <a-divider type="vertical"/>
                <a-icon type="form" :style="{ fontSize: '16px', color: '#1890FF',cursor:'pointer'}" @click="onEdit(record)" :title="$t('loss.edit')"/>
                <a-divider type="vertical" />
                <a-icon type="delete" :style="{ fontSize: '16px', color: '#1890FF',cursor:'pointer'}" @click="onRemove(record)" :title="$t('loss.action3')"/>
              </span>
            </span>
          </a-table-column>
        </s-table>
      </a-card>
      <loss-edit ref="lossEdit" @ok="onSearch"></loss-edit>
      <loss-add ref="lossAdd" @ok="onSearch"></loss-add>
      <add-point ref="addPoint" @ok="onSearch"></add-point>
      <search-substation ref="selectSub" @ok="loadSubData"></search-substation>
      <search-main ref="selectMain" @ok="loadMainData"></search-main>
    </div>
  </a-spin>
</template>
<script>
  import STable from '@/components/table/'
  import Dict from '@/views/public/Dict'
  import { axios } from '@/utils/request'
  import LossEdit from '@/views/loss/model/four/LossEdit.vue'
  import LossAdd from '@/views/loss/model/four/LossAdd.vue'
  import AddPoint from '@/views/loss/model/four/AddPoint.vue'
  import SearchSubstation from '@/views/public/SearchSubstation'
  import SearchMain from '@/views/public/SearchMain'
  export default {
    components: {
      STable,
      Dict,
      LossEdit,
      LossAdd,
      AddPoint,
      SearchSubstation,
      SearchMain
    },
    data () {
      return { 
        loading: false,
        show: true,
        queryParam:{stationCode:'',code:''},
        selectedRows: [],
        selectedRowKeys:[],
        filters: [{text: this.$t('common.yes'),value: '1'},{text: this.$t('common.no'),value: '0'}],
        loadData: parameter => {     
              const that = this
              parameter.generationFlag instanceof Array?that.queryParam.generationFlag = parameter.generationFlag[0]:'0'    
              parameter.sort='5' 
             return axios({
              url: '/loss/model/main/search',
              method: 'get',
              params: Object.assign(parameter, this.queryParam)
            }).then(res => { 
              return res.result
            })
        }
      }
    },
    methods: {
      SelectMain(){
        this.$refs.selectMain.showModal()
      },
      loadMainData(item) {
        this.queryParam.code = item[0]?item[0].code:''
        this.$refs.table.refresh()
      },
      SelectSub(){
        this.$refs.selectSub.showModal()
      },
      loadSubData(item) {
        this.queryParam.stationCode = item[0]?item[0].stationCode:''
        this.$refs.table.refresh()
      },
      onSearch() {
        this.$refs.table.refresh()
        this.selectedRows=[]
        this.selectedRowKeys=[]
      },
      showSearch() {
        this.show = !this.show
      },
      onCreate(record){
        let params=[]
        if(record){
          params[0]=record
        }else{
          params=this.selectedRows
        }
        if(params.length==0){
          this.$message.info(this.$t('common.selectone'))
          return
        }
        this.$refs.lossAdd.showModal(params,'5')
      },
      onEdit(record){
        let params=[]
        if(record){
          params[0]=record
        }else{
          params=this.selectedRows
        }
        if(params.length==0){
          this.$message.info(this.$t('common.selectone'))
          return
        }
        this.$refs.lossEdit.showModal(params,'5')
      },
      onAdd(record){
        this.$refs.addPoint.showModal(record,'5')
      },
      onRemove (record) {
        var _this = this
        let params=[]
        if(record){
          params[0]=record.id
        }else{
          _this.selectedRows.forEach(item => {
            params.push(item.id)
          })
        }
        if(params.length==0){
          this.$message.info(this.$t('common.selectone'))
          return
        }
        params=params.join(',')
        axios({
          method: 'delete',
          url: '/loss/model/main/delete',
          params: {id: params}
        }).then(function () {            
          _this.$refs.table.refresh()
        })
      },
      onSelect(selectedRowKeys, selectedRows) {
        this.selectedRows = selectedRows
        this.selectedRowKeys = selectedRowKeys
      },
    }
  }
</script>
<style lang="scss">
.error {
  color: red;
}
.normal {
  color: #00660f;
}
.ant-card-head-title{
  padding:12px 0
}
</style>