<template>
  <a-modal 
    :maskClosable="false" 
    :title="$t('common.selectmain')" 
    :width="1000" 
    v-model="isShow"
    @ok="onSubmit"
    @cancel="cancel" 
    :destroyOnClose="true">
    <div class="table-page-search-wrapper">
      <a-form layout="inline">
        <a-row :gutter="48">
          <a-col :md="10" :sm="24">
            <a-form-item >
              <a-input v-model="queryParam.mainCodeName" :placeholder="$t('maintransformer.placeholder')" style="width: 100%" @search="onSearch"/>
            </a-form-item>
          </a-col>
          <a-col :md="4" :sm="48">
            <span class="table-page-search-submitButtons">
              <btn-search @click="onSearch" :showInput="false"></btn-search>
            </span>
          </a-col>
        </a-row>
      </a-form>
      <s-table  
        ref="table"
        :bordered="false"
        :autoLoad="true"
        :data="loadData"
        size="small"
        rowKey="id"
        :rowSelection="{type: multiple ? 'checkbox' : 'radio', fixed: false, onChange: onSelect}"
      >
        <a-table-column 
          :title="this.$t('task.index1')"
          width="50px"
          align="center"
        >
          <span slot-scope="text">
            <a-icon type="schedule" theme="twoTone" class="icon" style="font-size:25px"/>
          </span>
        </a-table-column>
        <a-table-column
          :title="this.$t('maintransformer.code')"
          width="150px"
          dataIndex="code"
        ></a-table-column>
        <a-table-column
          :title="this.$t('maintransformer.name')"
          width="150px"
          dataIndex="name"
        ></a-table-column>
        <a-table-column
          :title="this.$t('line.info1')"
          width="120px"
          dataIndex="description"
        >
        </a-table-column>
        <a-table-column
          :title="this.$t('common.status')"
          width="150px"
          dataIndex="status"
          :filters="filters"
          :filterMultiple="false"
          @FilterDropdownVisibleChange="onChange(2)"
        >
          <span slot-scope="text,record">
            <span v-if="record.status==0">{{ $t('dct.DEVICE_STATUS0') }}</span>
            <span v-if="record.status==1">{{ $t('dct.DEVICE_STATUS1') }}</span>
            <span v-if="record.status==2">{{ $t('dct.DEVICE_STATUS2') }}</span>
          </span>
        </a-table-column>
      </s-table>
      <dict type="DEVICE_STATUS" table="params" ref="deviceStatus" v-show="false" />
    </div>
  </a-modal>
</template>

<script>
  import STable from '@/components/table/'
  import Dict from '@/views/public/Dict'
  import { isFunction } from 'util'
  import {
    axios
  } from '@/utils/request'
  export default {
    components: {
      STable,
      Dict 
    },
    props: {
      multiple: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        selectedRows: [],
        data: {},
        isShow: false,
        queryParam: {},
        cashData: {},
        selectRows: [],
        autoLoad: true,
        filters: [{text: 'none',value: '0'}],
        isAdmin: false,
        isEnding:false,
        callback: null,
        loadData: (parameter) => {    
          const that = this    
          parameter.status instanceof Array?that.queryParam.status = parameter.status[0]:'0'     
          return axios({
            method: 'get',
            url: '/archives/structure/maintransformer/search-list-main',
            params: Object.assign(parameter, this.queryParam)
          }).then(res => {
            return res.result
          })
        }
      }
    },
    methods: {
      showModal(params, callback) {
        this.isShow = true
        this.queryParam = {
          id: params?params.id:'',
          stationId:params?(params.stationId?params.stationId:''):''
        }
        this.selectedRowKeys = this.selectedRows = []
        this.isAdmin = params?params.isAdmin:false
        this.isEnding=params?params.isEnding:false
        this.callback = callback
      },
      onSearch() {
        this.$refs.table.refresh()
      },
      onSubmit () {        
        if(this.selectedRows.length) {
          this.$emit('ok',this.selectedRows,this.searchWord)
        }
        if(isFunction(this.callback)){
          this.callback(this.selectedRows)
          this.visible = false
        }
        this.isShow = false
      },
      cancel() {
        this.isShow = false
      },
      onSelect(selectedRowKeys, selectedRows) {
        this.selectedRows = selectedRows
      },
      onChange() {
        this.filters = this.$refs.deviceStatus.getAll()
      },
    },
  }
</script>
